*{
  margin: 0;
  padding: 0;
}

.App{
  width: 100%;
  height: 100%;
  min-height: 100vh;
  text-align: center;
  background-image: linear-gradient(180deg, rgba(210, 190, 153, 1), rgba(194, 255, 233, 1));
}

h1{
  padding-top: 70px;
  padding-bottom: 45px;
  font-size: 40px;
}
form{
  display: flex;
  justify-content: center;
}
input{
  font-size: 1.2rem;
  padding: 5px;
  outline: none;
  width: 30%;
  height: 35px;
  border: 1px solid gray;
  border-radius: 3px;
  box-sizing: border-box;
}
button{
  width: 3%;
  height: 35px;
  display: block;
  margin-left: 7px;
  cursor: pointer;
  border: 1px solid gray;
  border-radius: 3px;
}
button:hover{
  opacity: 0.7;
}